// 
const pathPre = '/vip'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/vip/Main.vue'))
        })
    },
    children: [
        // // 
        {
            path: 'list',
            name: 'vip-list',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/vip/List.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        // // 
        {
            path: 'gov',
            name: 'gov-vip',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/vip/govVip.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'vip/pay', // 
            name: 'vip-pay',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/vip/govVipForm.vue'))
                })
            },
            meta: {
                title: 'VIP-',
            }
        },
    ]
}



