// 
const pathPre = '/live'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/live/Main.vue'))
        })
    },
    children: [
        // // 
        {
            path: 'list',
            name: 'live-list',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/Live.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'add', // 
            name: 'live-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/LiveForm.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
        {
            path: 'edit/:id', // 
            name: 'live-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/LiveForm.vue'))
                })
            },
            meta: {
                title: '',
            }
        },

        // // 
        {
            path: 'comment/:id',
            name: 'live-comment',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/Comment.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'comment/add', // 
            name: 'live-comment-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/CommentForm.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
        {
            path: 'comment/edit/:id', // 
            name: 'live-comment-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/live/CommentForm.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
    ]
}



