// 

const pathPre = '/financemanage'

export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/financeManage/Main'))
        })
    },
    children: [
        {
            path: 'order',
            name: 'finance-order',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/Order.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'product',
            name: 'finance-product',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/product/Product.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'product/add', // -
            name: 'finance-product-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/product/ProductForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'product/edit/:id', // -
            name: 'finance-product-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/product/ProductForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        //
        {
            path: 'coupon',
            name: 'finance-coupon',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/coupon/Coupon.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'coupon/add', // -
            name: 'finance-coupon-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/coupon/CouponForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'coupon/edit/:id', // -
            name: 'finance-coupon-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/coupon/CouponForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'redPacket',
            name: 'finance-redPacket',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/financeManage/finance/redPacket.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        }
    ]
}



